import { IClonedEvent } from '@ms/odsp-utilities-bundle';
import { ClonedEventType as ClonedEventTypeEnum } from '@ms/odsp-utilities-bundle';
import { ResultTypeEnum } from '@ms/odsp-utilities-bundle';
import { CorrelationVector } from '@ms/odsp-utilities-bundle';
import { IEventMetadata } from '@ms/odsp-utilities-bundle';

/* tslint:disable:export-name */
export class CloneEventData {
/* tslint:enable:export-name */
  public resultCode: string;
  public resultType: ResultTypeEnum;
  constructor(resultCode: string, resultType: ResultTypeEnum) {
    this.resultCode = resultCode;
    this.resultType = resultType;
  }
}

class CloneEventUtils implements IClonedEvent {
  public id: number;
  public parentId: number = -1;
  public eventName: string = 'Qos,';
  public shortEventName: string = 'Qos';
  public startTime: number = 0;
  public endTime: number = 0;
  public enabled: boolean = true;
  public critical: boolean = false;
  public vector: CorrelationVector = undefined;
  public metadata: { [key: string]: IEventMetadata } = undefined;
  public validationErrors: number = -1;
  public data: CloneEventData;
  public eventType: ClonedEventTypeEnum;
  constructor(id: number, data: CloneEventData, eventType: ClonedEventTypeEnum) {
    this.id = id;
    this.data = data;
    this.eventType = eventType;
  }
}

export default CloneEventUtils;

